body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #e8f9fd;
}

.App {
  width: 600px;
  margin: 14rem auto;
  min-height: 250px;
  background-color: #59ce8f;
  color: #e8f9fd;
  border-radius: 15px;
  box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 2px rgb(255, 255, 255),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: space-around;
  font-size: 20px;
}

.question-section {
  display: flex;
  flex-direction: column;
  margin: 10px;
  width: 50%;
  overflow-wrap: break-word;
}

.question-count span {
  font-size: 40px;
}

.question-text {
  margin-top: 10px;
}

.answer-section {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.answer-section button {
  border: 2px solid #e8f9fd;
  padding: 8px 50px;
  border-radius: 20px;
  color: #e8f9fd;
  transition: 0.5s;
  background: transparent;
  cursor: pointer;
}

.answer-section button:hover {
  background-color: #e8f9fd;
  color: #59ce8f;
}

.score-section {
  margin: 100px;
}
